import React from 'react';
import './App.css';
import Navbar from './components/navbar';
import Home from './pages/Home';
import Room from './pages/Room'; //  Room component
import Foot from './pages/Foot';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'; //  Router, Routes, Route  react-router-dom

function App() {
  return (
    <div className="App">
      <Router>
        {/* Navbar  */}
        <Navbar />

        {/*  Routes  */}
        <Routes>
          {/*  (Home)  URL "/" */}
          <Route path="/home" element={<Home />} />
          
          {/*  (Room)  URL "/room" */}
          <Route path="/room" element={<Room />} />
        </Routes>

        {/* Footer  */}
        <Foot />
      </Router>
    </div>
  );
}

export default App;
